import View from './view';

class FighterView extends View {

  constructor(fighter, handleClick?) {
    super();
    this.createFighter(fighter, handleClick);
  }
  public getElement(){
    return super.getElement();
  }

  createFighter(fighter, handleClick?) {
    const { name, source } = fighter;
    const nameElement = this.createName(name);
    const imageElement = this.createImage(source);

    super.setElement(super.createElement({ tagName: 'div', className: 'fighter' }));
    super.getElement().append(imageElement, nameElement);
    if(handleClick)
    super.getElement().addEventListener('click', event => handleClick(event, fighter), false);
  }

  createName(name) {
    const nameElement = super.createElement({ tagName: 'span', className: 'name' });
    nameElement.innerText = name;

    return nameElement;
  }

  createImage(source) {
    const attributes = { src: source };
    return super.createElement({
      tagName: 'img',
      className: 'fighter-image',
      attributes
    });
  }
}

export default FighterView;